import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Article } from '../../modules/articles/models/Article';
import { ArticlesService } from '../../modules/articles/articles.service';
import { formatDate } from '@angular/common';

@Component({
    selector: 'meedias-redactor-article',
	templateUrl: './article.template.html',
	styleUrls: ['./article.styles.scss']
})
export class ArticleComponent implements OnInit {

	constructor(
		private articleService: ArticlesService
	){}

	//@Output() articleDeleted = new EventEmitter();
	@Output() articleDeleted = new EventEmitter<string>();
	@Input() loadedArticle: Article;
	article: Article;
	loaded: boolean;

	ngOnInit(){
		let article = this.loadedArticle;
		this.loaded = false;

		this.articleService.getArticle(this.loadedArticle.id).then((data) => {
			//console.log(data);
			article.name = data.name;
			article.categories = data.categories
			article.contents = data.contents;
			article.description = data.description;
			article.date = this.humanReadableDate(data.datum);
			this.loaded = true;
		});
		this.article = article;
	}

	deleteArticle(id){
		this.loaded = false;
		this.articleService.deleteArticle(id).then(() => {
			this.articleDeleted.emit(id);
		})
	}


	//Nothin too fancy here rn
	humanReadableDate(date): any {
		let nonReadableDate: any = new Date(date); // type any
		var delta = Math.round((+new Date - nonReadableDate) / 1000);
		var minute = 60,hour = minute * 60,day = hour * 24,week = day * 7;

		var humanReadableDate;

		if (delta < 30) { humanReadableDate = 'právě ted';
		} else if (delta < minute) { humanReadableDate = 'před ' + delta + ' sekundami' ;
		} else if (delta < 2 * minute) { humanReadableDate = 'před minutou'
		} else if (delta < hour) { humanReadableDate = 'před ' + Math.floor(delta / minute) + ' minutami';
		} else if (Math.floor(delta / hour) == 1) { humanReadableDate = 'před hodinou'
		} else if (delta < day) { humanReadableDate = 'před ' +Math.floor(delta / hour) + ' hours ago.';
		} else if (delta < day * 2) { humanReadableDate = 'včera';
		} else if (delta < day * 6) { humanReadableDate = 'tenhle týden';
		} else if (delta < week) { humanReadableDate = 'minulý týden'; }
		return humanReadableDate;
	}

}
